@import "../../variables.scss";

.portfolio-section {
  flex: 1;
  display: flex;
  position: relative;
  padding: 10px 30px;
  //overflow: auto;

  .articles-container {
    display: flex;
    flex-wrap: wrap;
    flex: 1;

    &:hover {
      .article-container {
        opacity: .5 !important;

        &:hover {
          opacity: 1 !important;
        }
      }
    }
  }
}

@media (max-width: 1000px) {

  .portfolio-section {
    padding: 10px 0;

    .articles-container {
      display: flex;
      flex-wrap: wrap;
      flex: 1;

      &:hover {
        .article-container {
          opacity: 1 !important;
        }
      }
    }
  }

}