@import "../../variables.scss";

.contact-section {
  display: flex;
  flex-direction: column;
  padding: 70px 0;

  .message {
    display: flex;
    margin-bottom: 30px;
    flex-direction: column;

    .contact-hand {
      fill: $white;
      height: 90px;
      margin-right: 20px;
      transform: rotate(15deg);
      animation-name: emojiShake;
      animation-iteration-count: infinite;
      animation-duration: 5s;
      flex-shrink: 0;
    }

    h1 {
      font-size: 40px;
      font-weight: $blackFontWeight;
    }

    p {
      font-size: 26px;
      font-weight: $semiBoldFontWeight;
      line-height: 20px;
      margin-top: 5px;
    }
  }

  .contact-links {
    display: flex;
    flex-wrap: wrap;
    padding: 50px 0;

    .contact-link {
      position: relative;
      font-size: 20px;
      color: $white;
      text-decoration: none;
      font-weight: $boldFontWeight;
      display: flex;
      align-items: center;

      &:before {
        content : '';
        position: relative;
        width: 10px;
        height: 10px;
        display: flex;
        background-color: $white;
        margin-right: 5px;
        border-radius: 100%;
        top: 2px;
        transition: all .1s;
      }


      $contactLinks : ("github", "linkedin", "puffysticks");
      $colors : (
              github : $githubColor,
              linkedin : $linkedinColor,
              puffysticks : $puffysticksColor
      );

      @each $link in $contactLinks {

        &.#{$link} {

          &:before {
            background-color: map-get($colors, $link);
          }

          &:hover {
            &:before {
              background-color: lighten(map-get($colors, $link), 5%);
              box-shadow: 0 0 20px 3px rgba(map-get($colors, $link), 1);
            }
          }
        }


      }

      &:not(:last-child) {
        margin-right: 20px;
      }
    }
  }
}



@keyframes emojiShake {
  0%, 35%, 60%{
    transform: rotate(15deg);
  }
  40%, 50% {
    transform: rotate(30deg);
  }
  45%, 55% {
    transform: rotate(-0deg);
  }
}


@media (max-width: 1000px) {

  .contact-section {
    padding: 30px 0;

    .message {

      .contact-hand {
        fill: $white;
        height: 50px;
      }
    }
  }

}

@media (max-width: 520px) {

  .contact-section {
    padding: 30px 0;

    .message {

      .contact-hand {
        fill: $white;
        height: 50px;
      }

      h1 {
        font-size: 30px;
      }
    }
  }

}