@import "../../../variables.scss";

$articleBacklight : $purple;

.article-container {
  position: relative;
  height: 200px;
  width: calc(25% - 20px);
  border-radius: 10px;
  margin: 10px;
  background-size: cover;
  background-position: center;
  cursor: pointer;
  opacity: 0;
  transform: translateY(-5px);
  transition: all .3s;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &:hover {
    opacity: 1;
    z-index: 2;
    transform: translateY(-2px) !important;

    .shadow {
      transform: scale(.9);
      opacity: .7;
      border-radius: 30px;
    }

    .img {
      filter: brightness(105%);
      box-shadow: 0 0 7px 3px rgba(black, .2);
    }
  }

  &:active {
    transition: all .1s;
    transform: scale(1);
  }

  .shadow {
    position: absolute;
    z-index: -2;
    top: 10px; left: 0;
    width: 100%;
    filter: blur(60px);
    opacity: 0;
    pointer-events: none;
    height: 100%;
    transform: scale(.8);
    border-radius: 10px;
    transition: all .5s;
  }

  .img {
    position: absolute;
    top: 0; left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    border-radius: 10px;
    transition: all .5s;
    overflow: hidden;
  }

  .sale-badge {
    position: absolute;
    z-index: 3;
    right: 10px;
    bottom: 10px;
    font-weight: $boldFontWeight;
    font-size: 14px;
    background-color: rgba($black, .7);
    backdrop-filter: blur(5px);
    padding: 5px 10px;
    border-radius: 20px;
  }

}

@media (max-width: 1370px) {

  .article-container {
    width: calc(100%/3 - 20px);
  }

}

@media (max-width: 1150px) {

  .article-container {
    width: calc(50% - 20px);
  }

}

@media (max-width: 1000px) {

  .article-container {
    width: calc(100%/3 - 20px);
  }

}

@media (max-width: 750px) {

  .article-container {
    width: calc(50% - 20px);
  }

}

@media (max-width: 600px) {

  .article-container {
    width: 100%;
    margin: 10px 0;
  }

}