@import url('https://fonts.googleapis.com/css?family=Muli:300,400,600,700,900&display=swap');
@import '../../variables.scss';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

::selection {
  background-color: $purple;
  color: $white;
}

::-webkit-scrollbar {
  width: 7px;
  height: 7px;
  background-color: lighten($black, 0%);
  transition: all .3s;

  &:hover {
    background-color: lighten($black, 1%);
  }
}

::-webkit-scrollbar-thumb {
  background-color: lighten($black, 10%);
  transition: all .3s;
}

.no-select {
  user-select: none;
}

body {
  font-family: $fontFamily;
  background-color: $bgColor;
  color: $white;
}

.main-wrap {
  position: relative;
  display: flex;
  width: 1500px;
  max-width: calc(100% - 150px);
}

.index-page-container {
  position: relative;
  width: 100%;
  //height: 100vh;
  display: flex;
  justify-content: center;

  .main-wrap {
    //padding: 50px 0;
    align-items: stretch;
    height: 100%;
  }

  .main-header {
    display: flex;
    align-items: center;
    padding-top: 70px;
  }
  
  .left-section {
    position: relative;
    width: 400px;
    flex-shrink: 0;
    margin-right: 20px;

    .sticky {
      position: sticky;
      display: flex;
      flex-direction: column;
      height: 100vh;
      justify-content: space-between;
      top: 0;

      .sticky-top {
        display: flex;
        flex-direction: column;
      }
      .sticky-bottom {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        flex: 1;

        .copyright {
          padding: 100px 0;
          opacity: .2;
        }
      }
    }
  }

  .right-section {
    position: relative;
    //overflow: hidden;
    max-width: 1250px;
    flex: 1;
  }
}


@media (max-width: 1000px) {

  .main-wrap {
    flex-direction: column;
  }

  .index-page-container {

    .main-header {
      padding-top: 50px;
    }

    .left-section {
      width: 100%;
      margin-right: 0px;
      margin-bottom: 20px;

      .sticky {
        position: relative;
        height: auto;

        .sticky-bottom {
          display: none;
        }
      }
    }
  }

}

@media (max-width: 520px) {

  .main-wrap {
    flex-direction: column;
    max-width: calc(100% - 70px);
  }

  .index-page-container {

    .main-header {
      padding-top: 50px;
    }

    .left-section {
      width: 100%;
      margin-right: 0px;
      margin-bottom: 20px;

      .sticky {
        position: relative;
      }
    }
  }

}