@import "../../variables.scss";

$animationDelay : .1s;

.article-page-container {
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: $black;
  display: flex;
  justify-content: center;
  top: 0; left: 0;
  overflow: auto;
  z-index: 5;

  .article-page {
    position: relative;
    min-height: 100vh;
    height: auto;
    align-self: flex-start;

    .left-section {
      position: sticky;
      top: 0;
      height: 100%;

      .sticky {
        padding: 150px 0;
        opacity: 0;
        height: auto;
        animation-fill-mode: forwards;
        animation-name: fadeIn;
        animation-duration: .5s;
        animation-delay: $animationDelay;

        .article-title {
          font-size: 40px;
          font-weight: $blackFontWeight;
          padding: 30px 0;
        }

        .article-description {
          font-size: 16px;
          //font-weight: $boldFontWeight;
          line-height: 25px;
          margin-bottom: 30px;
        }

        .sell-message {
          font-size: 16px;
          font-weight: $boldFontWeight;
          padding: 10px 20px;
          margin-top: 30px;
          border-radius: 20px;
          color: $black;
          background-color: rgba($white, 1);
        }

        .article-btns {
          display: flex;
          flex-wrap: wrap;

          .btn {
            padding: 10px 20px;
            border-radius: 20px;
            font-size: 16px;
            font-weight: $boldFontWeight;
            text-decoration: none;
            color: $white;
            box-shadow: inset 0 0 0 3px $white;
            cursor: pointer;
            white-space: nowrap;
            transition: all .2s;
            margin-bottom: 10px;

            &.primary {
              color: $black;
              background-color: $white;
              box-shadow: 0 0 0px $white;

              &:hover {
                background-color: $white;
                box-shadow: 0 0 10px $white;
              }
            }

            &:hover {
              background-color: rgba($white, .1);
            }

            &:not(:last-child) {
              margin-right: 20px;
            }
          }
        }
      }
    }

    .right-section {
      padding: 20px 30px;
      min-height: 100%;
      position: relative;
      align-self: flex-start;

      .article-content {
        animation-name: fadeIn;
        animation-duration: .7s;
        animation-delay: $animationDelay;
        animation-fill-mode: forwards;
        opacity: 0;
        flex: 1;
        position: relative;
        padding: 20px 30px;
        display: flex;
        flex-direction: column;
        overflow: hidden;
        border-radius: 10px;
        min-height: 100%;

        * {
          max-width: 100%;
        }

        img {
          object-fit: contain;
        }

        h1 {
          font-size: 40px;
          font-weight: $blackFontWeight;
          border-bottom: 1px solid rgba($white, .2);
          padding: 10px 0;
          margin-bottom: 10px;
        }

        h3 {
          margin-bottom: 10px;
        }

        blockquote {
          font-size: 16px;
          line-height: 23px;
          padding: 10px;
          background-color: rgba($white, .08);
          border-radius: 5px;
          margin-bottom: 10px;
        }

        p {
          margin: 5px 0;
          line-height: 25px;
        }

        a {
          padding: 2px 5px;
          background: $purple;
          color: $white;
          border-radius: 10px;
          text-decoration: none;
          box-shadow: inset 0 -30px 25px -25px $pink;
          transition: all .2s;

          &:hover {
            box-shadow: inset 0 -30px 25px -25px $pink,
            0 0 20px $purple;
          }
        }
      }
    }
  }
}


@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(-15px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}


@media (max-width: 1000px) {

  .article-page-container {

    .article-page {

      .left-section {
        position: relative;

        .sticky {
          padding: 30px 0;

          .article-title {
            font-size: 40px;
            padding: 20px 0;
          }

          .article-description {
            font-size: 16px;
            //font-weight: $boldFontWeight;
            line-height: 25px;
            margin-bottom: 20px;
          }

          .sell-message {
            padding: 10px 20px;
            margin-top: 20px;
          }
        }
      }

      .right-section {
        padding: 10px 0;
        min-height: 100%;
        position: relative;

        .article-content {
          position: relative;
          padding: 0;
        }
      }
    }
  }

}